#Home{
    background-image: url("bg.jpg");
    background-size:contain;
    top:0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;

  
}
#searchbox{
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-color: rgb(11, 82, 116);
   
}
#searchbox:focus{
  
   border: 3px solid rgb(39, 104, 245);
}
#search-btn{
    
    
   
    background-image: url("search.jpeg");
    background-size:contain;
    width: 110px;
    height: 37px;
    border: none;
    cursor: pointer;
    border-radius: 100px;
}
#search-btn:active{
   background-size: cover;
}
#container{
    margin-top: 100px;
}

.show-more-button.loading {
    background-color: rgb(235, 98, 98);
    cursor: not-allowed;
  }
  .show-more-button{
      background-image: url("showmore.png");
      background-size: cover;
      width: 100px;
      height: 30px;
      border: none;
      cursor: pointer;
      
  }
  img {
    border-radius: 8px; 
    margin-bottom: 10px;
    pointer-events:none;
    overflow: hidden;
   
  }
  
  p {
    font-size: 14px;
    color: #333;
    margin: 0;
    font-weight: bold;
  }



  